.row{
    margin-left: 20px;
    color: white;
}
.row-posters{
    display: flex;
    overflow-y: hidden; 
    overflow-x: scroll;
    padding: 20px;
}
.row-posters::-webkit-scrollbar{
    display: none;
}
.row-poster{
    width: 100%;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
    object-fit: contain;
    /*  keeps the aspect ratio */
}

.row-poster:hover{
    transform: scale(1.08);
}
.row-posterLarge{
    max-height: 250px;
}
.row-posterLarge:hover{
    transform: scale(1.09);
}